import { gql } from 'apollo-boost';

// @client    apolo 

export const LOG_USER_IN = gql`
  mutation logUserIn($token: String!) {
    logUserIn(token: $token) @client
  }
`;

export const LOG_USER_OUT = gql`
  mutation logUserOut($token: String!) {
    logUserOut @client
  }
`;



